var FPS = /** @class */ (function () {
    function FPS() {
        this.div = document.createElement('div');
        this.div.id = "FPS";
        document.body.appendChild(this.div);
    }
    FPS.prototype.updateFPS = function (fps) {
        this.div.innerHTML = Math.floor(fps).toString() + " fps";
    };
    return FPS;
}());
export default FPS;
