import { config, hudImgNames } from "../Config";
var HudBarFace = /** @class */ (function () {
    function HudBarFace() {
        this.faceStatusIndex = 1;
        this.direction = 2;
        this.currentGraphic = undefined;
        this.nextChange = Date.now() + config.faceRandomRangeSpeed * 4;
        this.randomGraphic();
    }
    HudBarFace.prototype.randomGraphic = function () {
        var _this = this;
        if (this.faceStatusIndex == 8) {
            this.direction = 2;
        }
        else {
            var tab = [1, 2, 3].filter(function (e) { return e != _this.direction; });
            this.direction = tab[Math.floor(Math.random() * tab.length)];
        }
        this.currentGraphic = hudImgNames.FACE.replace("_dir", "_" + this.direction).replace("_id", "_" + this.faceStatusIndex);
        this.nextChange = Date.now() + (Math.random() * config.faceRandomRangeSpeed * 2) - config.faceRandomRangeSpeed + config.faceBaseChangeSpeed;
    };
    HudBarFace.prototype.checkFaceStatus = function (camera) {
        if (camera.hp > 90)
            return 1;
        else if (camera.hp > 75)
            return 2;
        else if (camera.hp > 60)
            return 3;
        else if (camera.hp > 45)
            return 4;
        else if (camera.hp > 30)
            return 5;
        else if (camera.hp > 15)
            return 6;
        else if (camera.hp > 0)
            return 7;
        else
            return 8;
    };
    HudBarFace.prototype.update = function (ctx, camera) {
        var stat = this.checkFaceStatus(camera);
        if (stat != this.faceStatusIndex) {
            this.faceStatusIndex = stat;
            this.randomGraphic();
        }
        else if (Date.now() > this.nextChange) {
            this.randomGraphic();
        }
    };
    return HudBarFace;
}());
export default HudBarFace;
